<template>
  <div class="order-form">
    <!--<shop-header style="background-color: #fff;z-index: 999" title="我的订单"></shop-header>-->
    <order-nav :num-data="numData" @handleNav="handleNav" :active-index="activeIndex"></order-nav>
    <scroll :pullup="true"
            @scrollToEnd="loadMore"
            :data="list.length?list:orderList" ref="orderForm"
            class="order-scroll">
      <div>
        <div v-for="item in orderList" class="order-item-wrapper"
             :key="item.id">
          <div class="title-wrapper">
            <div class="title">袋鼠自营商城</div>
            <div class="state">
              <span v-show="item.isPay==='Y' && item.isShip==='N'">待发货</span>
              <span
                v-show="item.isPay==='Y' && item.isShip==='Y'&&item.isConfirmReceipt==='N'&&item.isVirtualOrder==='N'">待收货</span>
              <span v-show="item.isConfirmReceipt==='Y'">已完成</span>
              <span v-show="item.isPay==='N'">待付款</span>
            </div>
          </div>
          <OrderItem @detail="detail(item)" :item-data="i"
                     v-for="i in item.orderDetails"
                     :key="i.id"></OrderItem>
          <div class="order-price">共{{item.orderDetails.length}}件商品
            实付款:{{price(item.payTb,item.payCash)}}
          </div>
          <div class="order-item-bottom">
            <div @click="logistics(item.id)"
                 v-show="item.isPay==='Y'&&item.isShip==='Y'&&item.isVirtualOrder==='N'&&item.isConfirmReceipt==='N'">
              查看物流
            </div>
            <div
              v-show="item.isConfirmReceipt==='N'&&item.isVirtualOrder==='N'&&item.isShip==='Y'"
              class="pay" @click="confirmReceipt(item.id)">确认收货
            </div>

            <div @click="detail(item)"
                 v-show="item.isPay==='Y'&&item.isVirtualOrder==='N'">
              查看详情
            </div>
            <div @click="detail(item)"
                 v-show="item.rechargeType==='auto_ticket' && item.isVirtualOrder==='Y' && item.isConfirmReceipt==='Y'">
              前去使用
            </div>
            <div @click="quxiao(item.id)" v-show="item.isPay==='N'">取消订单</div>
            <div v-show="item.isPay==='N'" @click="toPay(item.id)" class="pay">
              立即支付
            </div>
          </div>
          <div v-show="item.payTime" class="time">
            {{formatDateing(item.payTime)}}
          </div>
        </div>

        <div v-if="orderList.length < 1" class="no-order">
          <div class="no-coupon">
            <div class="no-coupon-content fadeIn">
              <img src="../order-form/images/no-coupon.png" alt="no coupon"
                   class="">
              <p class="no-coupon-text">您还没有相关订单</p>
            </div>
          </div>
        </div>

        <div v-show="list.length" class="tuijian">
          <you-like class="you-like"></you-like>
          <div class="list-wrapper">
            <div class="list">
              <goods-item @toDetail="toDetail" @loadImage="loadImage"
                          v-for="item in list"
                          :key="item.id" class="list-item"
                          :data="item">
              </goods-item>
            </div>
          </div>
        </div>
        <loading v-show="showLoad"
                 class="loading"
                 :title="loadingTitle"></loading>
      </div>
    </scroll>
    <logistics @close="close" :logistics-data="logisticsData"
               v-show="showLogistics"></logistics>
  </div>
</template>

<script>
  import ShopHeader from '../../base/shop-header/shop-header'
  import OrderItem from '../../base/order-item/order-item'
  import OrderNav from '../../base/order-nav/order-nav'
  import Scroll from '../../base/scroll/scroll'
  import tool from '../../common/js/util'
  import YouLike from '../../base/you-like/you-like'
  import GoodsItem from '../../base/goods-item/goods-item.vue'
  import Logistics from '../../base/logistics/logistics'
  import Loading from '../../base/loading/loading'
  import * as core from '../../api/orderForm'

  export default {
    name: 'order-form',
    data() {
      return {
        activeIndex: 0,
        orderList: [],
        showLoad: true,
        loadingTitle: '正在加载...',
        currentPage: 1,
        pageSize: 10,
        list: [],
        logisticsData: null,
        showLogistics: false,
        numData:{
          unReceiptOrderCount: 0,
          unShipOrderCount: 0,
          unpaidOrderCount: 0
        }
      }
    },
    components: {
      ShopHeader,
      Scroll,
      OrderNav,
      OrderItem,
      Loading,
      YouLike,
      GoodsItem,
      Logistics
    },
    created() {
      this.getOrderCount()
      document.title = this.$route.meta.title     // console.log(this.$route.query.cartIds)
      // pageIndex:opts.currentPage,
      //   pageSize:opts.pageSize,
      //   shipStatus:opts.shipStatus,//待发货N
      //   payStatus:opts.payStatus,//代付款N
      //   confirmReceiptStatus:opts.confirmReceiptStatus,//是否收货N
      //   orderStatus:opts.orderStatus // 是否完成
      // this.getAllOrder({currentPage:1,pageSize:10,confirmReceiptStatus:'N'})
      this.selectObj = {currentPage: this.currentPage, pageSize: this.pageSize}
      this.getAllOrder(this.selectObj)
    },
    methods: {
      getOrderCount() {
        core.orderCount().then(res => {
          if (res.code && '00' === res.code) {
          this.numData=res.result
            // this.num = res.result.count
            console.log(res, 'dasdasdsadass')
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      formatDateing(e) {
        return tool.formatDate(e)
      },
      close() {
        this.showLogistics = false
      },
      logistics(e) {
        core.logistics({orderId: e}).then(res => {
          if (res.code && '00' === res.code) {
            if (res.result.success) {
              res.result.transferDetails = res.result.transferDetails.reverse();
              this.logisticsData = res.result;
              this.showLogistics = true
            } else {
              this.$toastBox.showToastBox(res.result.message)
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      toPay(id) {
        core.toPay({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            window.location.href = res.result.payUrl
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      toDetail(e) {
        console.log(e)
        this.$router.push({path: '/commodityDetails', query: {commodityId: e}})
      },
      loadImage() {
        if (this.timer) {
          clearTimeout(this.timer)
        }
        this.timer = setTimeout(() => {
          this.$refs.orderForm.refresh()
        }, 500)
      },
      loadMore() {
        if (!this.showLoad && !this.hasMore) {
          if (this.orderList.length > 0) {
            this.getAllOrder(this.selectObj)
          } else {
            this.getLikeGoods({
              currentPage: this.currentPage,
              pageSize: this.pageSize,
            })
          }

        }
      },
      getAllOrder(opts) {
        this.showLoad = true
        core.allOrder(opts).then(res => {
          console.log(res)
          if (res.code && '00' === res.code) {
            if (res.result.data.length > 0) {
              this.orderList = this.orderList.concat(res.result.data)
              this.showLoad = false;
              this.loadingTitle = '';
              this.selectObj.currentPage++;
              if (this.orderList.length >= res.result.amount) {
                this.hasMore = true
              }
            } else {
              this.getLikeGoods({
                currentPage: this.currentPage,
                pageSize: this.pageSize,
              })
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      getLikeGoods(opts) {
        this.showLoad = true
        core.likeGoods(opts).then(res => {
          if (res.code && '00' === res.code) {
            this.list = this.list.concat(res.result.data)
            this.loadingTitle = '';
            this.showLoad = false;
            this.currentPage++;
            if (this.list.length >= res.result.amount) {
              this.hasMore = true
            }
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      price(a, b) {
        return tool.priceStr(a, b)
      },
      quxiao(id) {
        core.quxiao({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            this.handleNav(this.activeIndex, true)
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      detail(item){
        if (item.isVirtualOrder==='Y' && item.isConfirmReceipt === 'Y' && item.rechargeType === 'auto_ticket'){
          this.$router.push({path:'/validCoupon'})
          return false
        }
        if(item.isVirtualOrder==='Y'){
          return false
        }
        this.$router.push({path:'/orderDetail',query:{orderId:item.id}})
      },
      confirmReceipt(id) {
        core.confirmReceipt({orderId: id}).then(res => {
          if (res.code && '00' === res.code) {
            this.handleNav(this.activeIndex, true)
          } else if (res.code && '01' === res.code) {
            tool.toWeiXinLogin(window.location.href)
          } else {
            this.$toastBox.showToastBox(res.message)
          }
        }).catch(e => {
          this.$toastBox.showToastBox(e)
        })
      },
      handleNav(e, state) {
        if (this.showLoad) {
          return false
        }
        if (this.activeIndex !== e || state) {
          this.activeIndex = e;
          this.orderList = [];
          this.showLoad = true;
          this.loadingTitle = '正在加载...';
          this.currentPage = 1;
          this.list = [];
          this.hasMore = false;
          if (e === 0) {
            this.selectObj = {
              currentPage: this.currentPage,
              pageSize: this.pageSize
            }
            this.getAllOrder(this.selectObj)
          }
          if (e === 1) {
            this.selectObj = {
              currentPage: this.currentPage,
              pageSize: this.pageSize,
              payStatus: 'N'
            }
            this.getAllOrder(this.selectObj)
          }
          if (e === 2) {
            this.selectObj = {
              currentPage: this.currentPage,
              pageSize: this.pageSize,
              shipStatus: 'N'
            }
            this.getAllOrder(this.selectObj)
          }
          if (e === 3) {
            this.selectObj = {
              currentPage: this.currentPage,
              pageSize: this.pageSize,
              confirmReceiptStatus: 'N'
            }
            this.getAllOrder(this.selectObj)
          }
          if (e === 4) {
            this.selectObj = {
              currentPage: this.currentPage,
              pageSize: this.pageSize,
              orderStatus: 'SUCCESS'
            }
            this.getAllOrder(this.selectObj)
          }
        }
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .order-form
    .order-scroll
      position fixed
      top 2.75rem
      /*top 0*/
      left 0
      z-index -1
      background-color #f5f5f5
      right 0
      bottom 0

      .no-order
        height 16.875rem
      .no-coupon
        position fixed
        top 0
        left 0
        right 0
        .no-coupon-content
          position absolute
          top 6rem
          width 9.5rem
          left 50%
          transform translateX(-50%)
          text-align center
          img
            width 9.5rem
            height 6.78125rem
          .no-coupon-text
            margin 0.75rem 0 0.9375rem 0
            font-size 0.75rem
            color rgb(153, 153, 153)

      .order-item-wrapper
        background-color #fff
        box-sizing border-box
        padding 0.75rem 4.27%
        margin-top 0.625rem
        .title-wrapper
          display flex
          font-size 0.813rem
          color #000
          flex-direction row
          padding-bottom 0.75rem
          justify-content space-between
          .state
            font-size 0.75rem
            color #ff4800
        .order-price
          text-align right
          font-size 0.75rem
          color #333
          padding-bottom 0.719rem
        .order-item-bottom
          display flex
          justify-content flex-end
          div
            font-size 0.75rem
            border-radius 0.688rem
            padding 0.313rem 0.719rem
            margin-left 1.063rem
            color #999
            border 1px solid #999
          .pay
            color #ff4800
            border-color #ff4800
        .time
          padding-top 0.375rem
          font-size 0.75rem
          color #999
    .tuijian
      padding 1rem 4.27% 0
      .you-like
        margin 0 0 0.375rem 0
      .list-wrapper
        text-align left
        width 100%
        padding 0.625rem 0 1rem
        box-sizing border-box
        margin 0 auto
        .list
          display flex
          justify-content space-between
          flex-wrap wrap
          padding-bottom 1rem
          .list-item
            width 49.43%
            overflow hidden
            border-radius 0.25rem
            margin-bottom 0.25rem
            break-inside: avoid;
</style>



